'use client';
import React, { useRef, useEffect } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

interface ExpandingSectionProps {
  children: React.ReactNode;
  heroRef: React.RefObject<HTMLDivElement | null>;
}

const ExpandingSection: React.FC<ExpandingSectionProps> = ({ children, heroRef }) => {
  const expandRef = useRef<HTMLDivElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!heroRef.current || !expandRef.current) return;

    // Initial state - showing 20% at bottom
    gsap.set(expandRef.current, {
      height: '20vh',
      y: 0,
      position: 'absolute',
      bottom: 0,
      width: '100%'
    });

    // Set initial content opacity to 0 (will fade in as section expands)
    gsap.set(contentRef.current, { opacity: 0 });

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: heroRef.current,
        start: 'bottom 80%',
        end: 'bottom top',
        scrub: 1,
        markers: false
      }
    });

    // Hero section moves up
    tl.to(heroRef.current, {
      y: '-80vh',
      ease: 'power2.inOut'
    }, 0);

    // Expanding section grows to full height
    tl.to(expandRef.current, {
      height: '100vh',
      y: '-80vh',
      ease: 'power2.inOut'
    }, 0);

    // Content fades in as section expands
    tl.to(contentRef.current, {
      opacity: 1,
      ease: 'power2.inOut'
    }, 0.3);

    // Reverse animation when scrolling back up
    ScrollTrigger.create({
      trigger: heroRef.current,
      start: 'top bottom',
      end: 'top top',
      scrub: 1,
      onEnterBack: () => {
        gsap.to(heroRef.current, { y: 0, ease: 'power2.inOut' });
        gsap.to(expandRef.current, { 
          height: '20vh', 
          y: 0,
          ease: 'power2.inOut' 
        });
        gsap.to(contentRef.current, { opacity: 0, ease: 'power2.inOut' });
      }
    });

    return () => {
      ScrollTrigger.getAll().forEach(t => t.kill());
    };
  }, [heroRef]);

  return (
    <div ref={expandRef} className="expanding-section">
      <div ref={contentRef} className="expanding-content">
        {children}
      </div>
    </div>
  );
};

export default ExpandingSection;